@import 'src/_variables';

// filters CSS is designed in stylesBooks.scss

.goodies {
    
    .goodie {

        position: relative;
        width: 300px;
        margin: 1rem auto;
        padding: $gutter;
        border: 2px solid $main_color_alt;

        @media (min-width: 700px) {
            margin: 1rem;
        }
        
        &-main-picture {
            position: relative;
            width: 300px;
            height: 300px;
            object-fit: cover;
            object-position: top;
        }

        &-alt-picture {
            position: absolute;
            top: 15px;
            right: 15px;
            width: 60px;
            height: 60px;
            margin: $gutter/2;
            object-fit: contain;
            object-position: center;
        }

        &-alt-type {
            position: absolute;
            top: 15px;
            left: 15px;
            min-width: 50px;
            min-height: 50px;
            line-height: 50px;
            font-size: $font-size*1;
            padding: 5px;
            margin: $gutter/2;
            background-color: $third-color;
            border-radius: 50%;
            box-shadow: 1px 1px 2px 2px #000;
        }

    }

    @media (min-width: 700px) {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
    }
}