@import 'src/_variables';

.header {
  background-size: cover;
  padding: $gutter $gutter * 2;
  // version desktop => padding: $gutter $gutter * 10;

  @media (min-width: 900px) {
    padding: $gutter $gutter * 4;
  }

  &-opacity {
    background-color: rgba(#000000, 0.8);
    border-radius: $gutter;
    padding: $gutter;

    @media (min-width: 900px) {
      padding: $gutter * 2;
      max-width: 1200px;
      margin: auto;
    }

    .header-title {
      display: flex;

      .burger {
        z-index: 5;
        line-height: 25px;
        width: 25px;
        height: 25px;
        color: $secondary-color-alt;
        font-size: $font-size * 3;
        padding: 5px;
        border-radius: 50%;
        position: fixed;
        top: 8px;
        background-color: rgba(#000, $alpha: 0.8);

        @media (max-width: 500px) {
          font-size: $font-size * 1.5;
        }
      }

      .banner {
        width: 60%;
        margin: auto;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }

  // Menu mobile
  &-navigation {
    z-index: 105;

    &-mobile {
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: left;
      position: fixed;
      top: 0;
      left: -600px;
      bottom: 0;
      background-color: rgba($color: #000000, $alpha: 0.9);
      animation: slide 0.3s forwards;

      @keyframes slide {
        100% {
          left: 0;
        }
      }
    }

    &-desktop {
      width: 100%;
      flex-direction: row;
      text-align: center;
      position: relative;
      background: none;
    }

    a {
      display: inline-block;
      color: $secondary-color-alt;
      margin: 0 1rem;
      padding: 0.8rem;
      border-radius: 10px;
      transition: background 0.25s, box-shadow 0.15s;

      &.active {
        background-color: $main-color;
        color: $third-color;
      }

      &:hover {
        color: $third-color;
        background: $main-color;
        box-shadow: 3px 3px 5px 0px $secondary-color-alt;
      }
    }
  }
}
