@import 'src/_variables';

.member-informations {

    border-bottom: 5px solid $main-color-alt;
    padding-bottom: 5rem;
    
    @media (min-width: 900px) {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
    }
 
    img {
        width: 300px;
        max-height: 400px;
        object-fit: cover;
        object-position: center;
    }
    
    p {
        width: 300px;
        max-height: 400px;
        margin: 1rem auto;
        overflow: auto;

        @media (min-width: 900px) {
            margin: 0;
        }
    }
}

.member-socials {
    margin: auto;
    display: flex;
    justify-content: center;
    border-bottom: 5px solid $main-color-alt;
    padding-bottom: $gutter * 3;

    a {
        margin: 0 1rem;
    }
}

.member-works {
    
    img {
        margin: 1rem;
        width: 300px;
        height: 400px;
        object-fit: cover;
        object-position: center;
    }
}