@import 'src/_variables';

.filter-zone {
  color: $third-color;
  background-color: rgba($secondary-color, 0.9);
  padding: $gutter/2 $gutter * 2;
  margin-bottom: $gutter;

  .filters {
    @media (min-width: 900px) {
      display: flex;
    }
  }

  .filter {
    display: flex;
    align-items: center;
    justify-content: left;

    .gender-logo {
      width: 10%;
      padding: 0 $gutter/2;

      @media (min-width: 900px) {
        width: 5%;
      }
    }
  }
}

.novels {
  .novel {
    width: 300px;
    margin: 1rem auto;
    padding: $gutter;
    border: 2px solid $main_color_alt;

    @media (min-width: 700px) {
      margin: 1rem;
    }

    &-header {
      background-color: rgba($main-color, 0.9);
      height: 15%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 1rem;

      & .novel-title {
        color: $third-color;
      }
    }

    &-main {
      height: 67%;
      position: relative;
      
      &-picture {
        width: 300px;
        height: 400px;
        object-fit: cover;
        object-position: center;
      }
    }

    &-alt {
      width: 300px;
      display: flex;
      justify-content: space-between;
      overflow-y: auto;
      height: 15%;

      &-picture {
        width: 70px;
        height: 70px;
        margin: $gutter/2 0;
        object-fit: cover;
        object-position: center;
      }
    }

    &-gender {
      width: 60px;
      z-index: 15;
      position: absolute;
      top: $gutter * 1.5;
      right: $gutter * 1.5;
    }
  }

  @media (min-width: 700px) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
