@import 'src/_variables';

.staff-listing {

    @media (min-width: 900px) {
        display: flex;
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
    }

    .staff {
        width: 200px;
        margin: $gutter *3 auto;
        position: relative;

        @media (min-width: 900px) {
            margin: 2rem;
        }

        &-selfie {
            width: 200px;
            height: 200px;
            object-fit: cover;
            object-position: center;
            filter: drop-shadow(5px 5px 5px $main-color);
        }

        &-role {
            position: absolute;
            top: 5px;
            right: 5px;
            width: 60px;
        }
    }
}