@import 'src/_variables';

.cart {
  @media (min-width: 900px) {
    width: 600px;
    margin: auto;
  }

  .listing {
    &-item {
      margin-top: $gutter * 3;
      padding: $gutter;
      border-bottom: 3px solid $main-color-alt;

      @media (min-width: 900px) {
        display: flex;
        justify-content: space-evenly;
      }

      &-description {
        display: flex;

        img {
          width: 100px;
          height: 100px;
          object-fit: cover;
          object-position: center;
          margin-right: 1rem;
        }

        p {
          text-align: left;
        }
      }

      &-price {
        display: flex;
        justify-content: space-between;

        p {
          min-width: 150px;
          text-align: right;
        }
      }
    }
  }

  .price {
    &-order,
    &-shipping,
    &-total {
      display: flex;
      justify-content: flex-end;
      padding: $gutter;

      .subtitle {
        font-weight: bold;
      }

      span {
        min-width: 100px;
        text-align: right;
      }
    }
  }
}
