@import 'src/_variables';

.item-details {
    text-align: left;

    h2 {
        text-align: center;
    }
}
.item-informations {
    
    @media (min-width: 900px) {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    &-left {
        display: flex;
        flex-direction: column;
        padding-bottom: $gutter * 5;
        border-bottom: 5px solid $main-color-alt;

        @media (min-width: 900px) {
            width: 50%;
        }

        .item-miniatures {
            width: 300px;
            overflow-x: auto;
            margin: auto;
            display: flex;
            justify-content: space-around;
            order: 1;
            
            .miniature {
                width: 72px;
                height: 72px;
                margin: 2px;
                object-fit: cover;
            }
        }

        .item-principale {
            width: 300px;
            height: 400px;
            margin: auto;
            object-fit: contain;
        }
    }

    &-right {
        padding-bottom: $gutter * 5;
        border-bottom: 5px solid $main-color-alt;

        @media (min-width: 900px) {
            width: 50%;
            padding: 0 $gutter * 3;
        }
    }
}

.item-action-zone {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: $gutter * 3;
    border-bottom: 5px solid $main-color-alt;

    @media (min-width: 900px) {
        flex-direction: row;

        button {
            width: 100%;
        }
    }
}

.item-similar-products {
    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: 900px) {
        flex-direction: row;
        justify-content: space-evenly;
    }

    .item-proposal {
        width: 200px;
        height: 200px;
        padding: 10px;
        object-fit: cover;
        object-position: center;
    }
}