@import 'src/_variables';

.illustration {
    text-align: center;

    &-title {
        text-decoration: underline $main-color-alt;
    }

    &-details {

        @media (min-width: 900px) {
            display: flex;
            justify-content: space-evenly;
        }
    }

    &-image {


        img {
            max-width: 300px;

            @media (min-width: 900px) {
                max-width: 900px;
            }
        }
    }
}