@import '../../../../variables';

.form {
  margin: auto;
  padding: 2rem;
  width: 500px;
  display: flex;
  flex-direction: column;
  background-color: $secondary-color;
  border-top: 6px solid $main-color-alt;
}

@media (max-width: 900px) {
  .form {
    width: 90%;
    padding: 1rem;
    margin: 1rem auto;
  }
}

.test {
  width: 300px;
  margin: 1rem;
  padding: 0.5rem;
  align-items: center;

  & .input-box {
    width: 100%;
  }
}
