@import 'src/_variables';

// filter CSS is defined in the stylesBooks.scss

.pictures-listing {
    display: flex;
    flex-direction: column;

    @media (min-width: 900px) {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }

    .picture-miniature {
        position: relative;
        width: 300px;
        height: 200px;
        margin: 1rem auto;

        @media (min-width: 900px) {
            margin: 1rem;
        }

        .picture-visuel {
            width: 300px;
            height: 200px;
            object-fit: cover;
            object-position: center;
        }

        // .picture-collection {
