@import 'src/_variables';

.novel-details {
    text-align: left;

    h2 {
        text-align: center;
    }
}
.novel-informations {
    
    @media (min-width: 900px) {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    &-left {
        display: flex;
        flex-direction: column;
        padding-bottom: $gutter * 5;
        border-bottom: 5px solid $main-color-alt;

        @media (min-width: 900px) {
            width: 50%;
        }

        .novel-miniatures {
            width: 300px;
            overflow-x: auto;
            margin: auto;
            display: flex;
            justify-content: space-around;
            order: 1;
            
            .miniature {
                width: 72px;
                height: 72px;
                margin: 2px;
                object-fit: cover;
            }
        }

        .novel-principale {
            width: 300px;
            height: 400px;
            margin: auto;
            object-fit: cover;
        }
    }

    &-right {
        padding-bottom: $gutter * 5;
        border-bottom: 5px solid $main-color-alt;

        @media (min-width: 900px) {
            width: 50%;
            padding: 0 $gutter * 3;
        }
    }
}

.novel-text-zone {
    padding-bottom: $gutter * 5;
    border-bottom: 5px solid $main-color-alt;
}

.novel-action-zone {
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media (min-width: 900px) {
        flex-direction: row;

        button {
            width: 100%;
        }
    }
}

.novel-comments-zone {

    display: flex;
    flex-direction: column;
    justify-content: center;

    .novel-comments {
        height: 300px;
        overflow-y: auto;
    }
}