@import '../../../variables';

.modal {
  z-index: 100;
  position: fixed;
  top: 10vh;
  left: 10%;
  width: 80%;
  max-height: 80vh;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
  padding: 0.2rem;
  padding-top: 1rem;
  min-height: 50%;
  overflow: auto;
}

@media (max-width: 900px) {
  .modal {
    width: 85%;
    left: 8%;
  }
}
