@import 'src/_variables.scss';

.pagination {
  display: flex;
  position: relative;
  bottom: 0;
  right: 0;
  text-decoration: none;
  margin: 1rem;

  &-item {
    list-style: none;
    margin: 0.5rem;
    width: 30px;
    border-radius: 15px;
    border: 1px solid $main-color-alt;

    &:hover {
      background-color: $main-color-alt;
      color: white;
    }

    &.active {
      background-color: $main-color-alt;
      color: white;
    }
  }

  &-link {
    cursor: pointer;
    height: 100%;
    width: 100%;
    display: block;
  }
}
