@import 'src/_variables.scss';

#profil {
  display: flex;
  flex-direction: column;
  margin: 0 calc(50% - 27rem);
  background-color: $secondary-color;
  border-top: 6px solid $main-color-alt;
  color: white;
  padding: 2rem;

  @media (max-width: 900px) {
    margin: 0.1rem;
  }

  &__info {
    display: flex;
    justify-content: space-around;
    margin: 1rem;
    position: relative;

    @media (max-width: 900px) {
      flex-direction: column;
      align-items: center;
    }

    &-avatar {
      width: 150px;
      height: 150px;
      overflow: hidden;
      border-radius: 50%;

      & > img {
        width: 150px;
      }

      @media (max-width: 900px) {
        margin-bottom: 2rem;
      }

      & svg {
        position: absolute;
        right: 65%;
        bottom: 0%;
        cursor: pointer;

        @media (max-width: 900px) {
          right: 0%;
          bottom: 55%;
        }
      }
    }

    &-information {
      display: flex;
      flex-direction: column;
      border-left: 4px solid $main-color-alt;
      padding-left: 1rem;
      text-align: left;

      & svg {
        position: absolute;
        right: 0%;
        bottom: 0%;
        cursor: pointer;
      }
    }
  }

  &__adresses {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: black;
    height: 10rem;
    text-align: left;
    margin: 1.5rem 0;

    @media (max-width: 900px) {
      height: 15rem;
    }

    & > * {
      padding: 0.3rem;
      max-height: 10rem;

      @media (max-width: 900px) {
        min-height: 6rem;
      }
    }

    & .adresses-item {
      display: flex;
      flex-wrap: wrap;
      border: 2px solid black;
      border-radius: 5px;
      width: 90%;
      margin: 0.3rem;
      background-color: white;

      & * {
        cursor: pointer;
      }

      & label {
        padding: 0.2rem;
      }

      &:hover {
        background-color: $main-color-alt;
        color: white;
        cursor: pointer;
      }
    }
  }

  &__change__profil {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 94%;

    & > * {
      border-top: 2px solid $main-color-alt;
      display: flex;
      justify-content: space-between;
      margin: 0.5rem;
      padding: 0.3rem;
      padding-left: 1rem;
      text-transform: uppercase;

      & > svg {
        padding: 1rem;
        cursor: pointer;
      }
    }
  }

  &__biography {
    width: 90%;
    text-align: left;
    margin: auto;
    position: relative;

    & svg {
      position: absolute;
      right: 68%;
      bottom: 77%;
      cursor: pointer;

      @media (max-width: 900px) {
        right: 0%;
        bottom: 89%;
      }
    }

    &-text-area {
      font-family: $text-font;
      width: 100%;
      height: 10rem;
      background-color: $secondary-color-alt;
      color: black;
      padding: 0.5rem;

      @media (max-width: 900px) {
        height: 30rem;
      }
    }
  }

  &__control {
    width: 90%;
    display: flex;
    flex-direction: column;
    margin: auto;

    & .btn {
      width: 40%;
      margin: 1rem;

      &.cart {
        background-color: $main-color;
      }

      &.products {
        background-color: green;
      }

      &.admin {
        background-color: red;
      }

      @media (max-width: 900px) {
        margin: 1rem;
        width: 85%;
      }
    }
  }
}
