@import 'src/_variables';

#footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: $third-color;
    padding: $gutter $gutter * 2;
    background-size: cover;
    background-position: center;

    @media (min-width: 900px) {
        padding: $gutter $gutter * 4;

    }

    &-opacity {
        background-color: rgba(#000, .7);
        padding: $gutter * 2;
        border-radius: $gutter;

        @media (min-width: 900px) {
            display: flex;
            justify-content: space-around;
            align-items: center;
            max-width: 1200px;
            margin: auto;
        }
    }
}

#network, #informations, #development {
    font-size: $gutter;

    @media (min-width: 900px) {
        font-size: $gutter * 1.2;
        margin: 0 3rem;
    }
}

#network {
    display: flex;
    justify-content: center;

    .network-social {
        margin: 0 1rem;
    }
    
    @media (min-width: 900px) {
        font-size: $font-size * 2.2;

        a:hover {
            color: $secondary-color-alt;
        }
    }
}

#informations {

    a {
        padding: 5px;
    }

    @media (min-width: 900px) {
        
        p {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-evenly;
        }
    
        a {
            padding: .5rem 1rem;
    
            &:hover {
                border-radius: 10px;
                color: $third-color;
                background: $main-color;
                box-shadow: 3px 3px 5px 0px $secondary-color-alt;
            }
        }
    }
}

#development {
    display: flex;
    justify-content: center;
    align-items: center;

    @media (min-width: 900px) {
        a:hover {
            color: $secondary-color-alt;
        }
    }

    .school {
        margin-left: 5px;
        width: 20px;
        background-color: $third-color;
        border-radius: 5px;

        @media (min-width: 900px) {
            &:hover {
                background-color: $secondary-color-alt;
            }
        }
    }
}


.element-width {

    @media (min-width: 900px) {
        width: calc(100%/3);
        align-items: center;
    }
}