@import '../../../../variables';

.input-box {
  margin: 0.4rem;
  display: flex;
  overflow: hidden;

  &.text-area {
    display: flex;
    flex-direction: column;
  }

  & .label {
    text-align: right;
    padding: 0.5rem;
    left: 0;
    width: 40%;
    background-color: $main-color-alt;
    color: white;

    &.text-area {
      width: 100%;
      text-align: left;
    }
  }

  & select.input {
    width: 94%;
  }

  & .form-textarea {
    resize: none;
    height: 10rem;
    font-family: $text-font;
  }

  & .input {
    border: none;
    outline: none;
    width: 90%;
    padding-left: 1rem;
    background-color: white;
    padding: 0.5rem;

    &:focus {
      box-shadow: inset 0 0 25px $secondary-color-alt;
    }
  }
}

@media (max-width: 900px) {
  .input-box {
    flex-direction: column;
    height: 4rem;

    &.text-area {
      height: 20rem;
    }

    & .label {
      text-align: left;
      width: 100%;
      height: 20%;

      &.text-area {
        width: 100%;
        height: 5%;
        text-align: left;
      }
    }

    & .input {
      height: 100%;
      width: 100%;
    }

    & select.input {
      width: 100%;
    }

    & .form-textarea {
      resize: none;
      height: 20rem;
    }
  }
}
