@import '../../../../variables';

button {
  position: relative;
  border: none;
  margin: 0.3em;
  outline: none;
  font-family: $title-font;
  cursor: pointer;
  background-color: $main-color-alt;
  margin-top: 2rem;
  padding: 1rem;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 1.3rem;
  color: white;
  transition: top ease 0.5s;

  &:hover {
    top: -5px;
    box-shadow: 0 5px 0 0 $secondary-color-alt;
  }

  &:active {
    transform: scale(0.95);
  }
}
