@import 'src/_variables';

#admin-navigation {
  width: 12vw;
  min-width: 150px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: $secondary-color;
  text-align: left;

  a {
    color: $secondary-color-alt;
    max-width: 100px;
    margin-left: 1rem;
    margin-top: 0.5rem;
    padding: 0.8rem;
    border-radius: 10px;
    transition: background 0.25s, box-shadow 0.15s;

    &:hover {
      color: $third-color;
      background: $main-color-alt;
      box-shadow: 3px 3px 5px 0px $secondary-color-alt;
    }

    &.active {
      background-color: $main-color-alt;
      color: $third-color;
    }
  }
}
